<template>
    <div v-if="splitResults && splitResults.length" class="card p-3">
      <h3>Contribution Breakdown</h3>
      <p><strong>Total Expenditure:</strong> {{ totalExpenditure }} kr</p>
      
      <table class="table table-striped">
        <thead>
          <tr>
            <th>Item</th>
            <th>Total Cost (SEK)</th>
            <th v-for="person in people" :key="person.name">{{ person.name || 'Person' }}</th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="(result, index) in splitResults" :key="index">
            <td>{{ result.description }}</td>
            <td>{{ result.totalAmount }}</td>
            <td v-for="(contribution, idx) in result.contributions" :key="idx">{{ contribution }} kr</td>
          </tr>
        </tbody>
      </table>
  
      <h3>Individual Totals</h3>
      <p>This section shows each person’s total contribution, savings, remaining salary, and the percentage of income spent.</p>
      <table class="table table-bordered">
        <thead>
          <tr>
            <th>Person</th>
            <th>Total Contribution (SEK)</th>
            <th>Saving (SEK)</th>
            <th>Remaining Salary (SEK)</th>
            <th>% of Salary Spent</th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="(personTotal, idx) in totalContributions" :key="idx">
            <td>{{ people[idx]?.name || `Person ${idx + 1}` }}</td>
            <td>{{ personTotal }} kr</td>
            <td>{{ savings[idx] }} kr</td>
            <td>{{ remainingSalaries[idx] }} kr</td>
            <td>{{ expenditurePercentages[idx] }}%</td>
          </tr>
        </tbody>
      </table>
    </div>
  </template>
  
  <script>
  export default {
    props: ["splitResults", "totalExpenditure", "totalContributions", "savings", "remainingSalaries", "expenditurePercentages", "people"],
  };
  </script>
  