<template>
    <div class="chart-container">
      <canvas ref="chart"></canvas>
    </div>
  </template>
  
  <script>
  import { Chart, registerables } from 'chart.js';
  
  Chart.register(...registerables);
  
  export default {
    props: {
      type: {
        type: String,
        required: true
      },
      labels: {
        type: Array,
        required: true
      },
      data: {
        type: Array,
        required: true
      },
      label: {
        type: String,
        required: true
      }
    },
    watch: {
      labels() {
        this.createChart();
      },
      data() {
        this.createChart();
      }
    },
    mounted() {
      this.createChart();
    },
    methods: {
      createChart() {
        if (this.chart) {
          this.chart.destroy(); // Clear the previous chart instance if any
        }
        this.chart = new Chart(this.$refs.chart, {
          type: this.type,
          data: {
            labels: this.labels,
            datasets: [
              {
                label: this.label,
                data: this.data,
                backgroundColor: [
                  '#ff6384', '#36a2eb', '#ffcd56', '#4bc0c0', '#9966ff', '#ff9f40'
                ],
                borderWidth: 1
              }
            ]
          },
          options: {
            responsive: true,
            maintainAspectRatio: false
          }
        });
      }
    },
    beforeUnmount() {
      if (this.chart) {
        this.chart.destroy();
      }
    }
  };
  </script>
  
  <style scoped>
  .chart-container {
    position: relative;
    height: 400px;
    width: 100%;
  }
  </style>
  