<template>
  <div class="container my-4">
    <h2 class="text-center mb-4">Expense Splitter with Data Visualization</h2>
    <p class="text-center">Easily split expenses among people based on their income and visualize distribution.</p>

    <!-- People Section and Items Section -->
    <PeopleSection :people="people" @add-person="addPerson" @remove-person="removePerson" @save-data="saveData" />
    <ItemsSection :items="items" :people="people" @add-item="addItem" @remove-item="removeItem" @save-data="saveData" />

    <!-- Calculate Split Button -->
    <button class="btn btn-success my-4" @click="calculateSplit">Calculate Split</button>

    <!-- Results Section -->
    <ResultsSection
      :splitResults="splitResults"
      :totalExpenditure="totalExpenditure"
      :totalContributions="totalContributions"
      :savings="savings"
      :remainingSalaries="remainingSalaries"
      :expenditurePercentages="expenditurePercentages"
      :people="people"
    />

    <!-- Expense Distribution by Item Pie Chart -->
    <div class="chart-wrapper mt-4">
      <h4>Expense Distribution by Item</h4>
      <ExpenseChart
        type="pie"
        :labels="itemLabels"
        :data="itemData"
        label="Expense Distribution by Item"
      />
    </div>

    <!-- Expense Distribution by Person (Breakdown of Items per Person) -->
    <div v-for="(person, index) in people" :key="index" class="chart-wrapper mt-4">
      <h4>Expense Breakdown for {{ person.name || `Person ${index + 1}` }}</h4>
      <ExpenseChart
        type="pie"
        :labels="itemLabels"
        :data="personItemData[index]"
        :label="`Contribution by Item for ${person.name || `Person ${index + 1}`}`"
      />
    </div>
  </div>
</template>

<script>
import PeopleSection from "./components/PeopleSection.vue";
import ItemsSection from "./components/ItemsSection.vue";
import ResultsSection from "./components/ResultsSection.vue";
import ExpenseChart from "./components/ExpenseChart.vue";

export default {
  components: {
    PeopleSection,
    ItemsSection,
    ResultsSection,
    ExpenseChart
  },
  data() {
    return {
      items: [{ description: "Rent", totalAmount: null, splits: [100], customSplitEnabled: false }],
      people: [{ name: "", income: null, savingEnabled: false, savingPercentage: 10 }],
      splitResults: [],
      totalExpenditure: 0,
      totalContributions: [],
      savings: [],
      remainingSalaries: [],
      expenditurePercentages: [],
      itemLabels: [],
      itemData: [],
      personLabels: [],
      personData: [],
      personItemData: []
    };
  },
  created() {
    this.loadData();
  },
  methods: {
    addItem() {
      this.items.push({ description: "", totalAmount: null, splits: Array(this.people.length).fill(0), customSplitEnabled: false });
      this.saveData();
    },
    removeItem(index) {
      this.items.splice(index, 1);
      this.saveData();
    },
    addPerson() {
      this.people.push({ name: "", income: null, savingEnabled: false, savingPercentage: 10 });
      this.items.forEach(item => item.splits.push(0));
      this.saveData();
    },
    removePerson(index) {
      this.people.splice(index, 1);
      this.items.forEach(item => item.splits.splice(index, 1));
      this.saveData();
    },
    calculateSplit() {
      // Calculate the total expenditure for all items
      this.totalExpenditure = this.items.reduce((total, item) => total + (item.totalAmount || 0), 0);

      // Calculate individual contributions based on income or custom split for each item
      this.splitResults = this.items.map((item) => {
        const contributions = this.people.map((person, personIndex) => {
          if (item.customSplitEnabled && item.splits[personIndex] !== undefined) {
            // Custom split mode
            return ((item.totalAmount * item.splits[personIndex]) / 100).toFixed(2);
          } else {
            // Income-based split
            const totalIncome = this.people.reduce((sum, p) => sum + p.income, 0);
            return ((item.totalAmount * person.income) / totalIncome).toFixed(2);
          }
        });
        return {
          description: item.description,
          totalAmount: item.totalAmount,
          contributions,
        };
      });

      // Calculate total contributions for each person
      this.totalContributions = this.people.map((_, personIndex) => {
        return this.splitResults.reduce((sum, result) => {
          return sum + parseFloat(result.contributions[personIndex] || 0);
        }, 0).toFixed(2);
      });

      // Prepare data for the item and person pie charts
      this.itemLabels = this.items.map(item => item.description);
      this.itemData = this.items.map(item => item.totalAmount || 0);
      this.personLabels = this.people.map(person => person.name || `Person`);
      this.personData = this.totalContributions.map(contribution => parseFloat(contribution) || 0);

      // Prepare item contribution breakdown for each person
      this.personItemData = this.people.map((_, personIndex) => {
        return this.splitResults.map(result => result.contributions[personIndex] || 0);
      });

      // Calculate savings and remaining salaries for each person
      this.savings = this.people.map((person) => {
        return person.savingEnabled
          ? ((person.income * person.savingPercentage) / 100).toFixed(2)
          : 0;
      });

      this.remainingSalaries = this.people.map((person, index) => {
        return (person.income - this.totalContributions[index] - this.savings[index]).toFixed(2);
      });
      this.expenditurePercentages = this.people.map((person, index) => {
        const expenditure = parseFloat(this.totalContributions[index]) + parseFloat(this.savings[index]);
        return ((expenditure / person.income) * 100).toFixed(2);
      });
    },
    saveData() {
      localStorage.setItem("items", JSON.stringify(this.items));
      localStorage.setItem("people", JSON.stringify(this.people));
    },
    loadData() {
      const savedItems = localStorage.getItem("items");
      const savedPeople = localStorage.getItem("people");
      if (savedItems) {
        this.items = JSON.parse(savedItems);
      }
      if (savedPeople) {
        this.people = JSON.parse(savedPeople);
      }
    },
  },
};
</script>

<style scoped>
.container {
  max-width: 800px;
}
.chart-wrapper {
  max-width: 800px;
  margin: 0 auto;
}
.card {
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}
</style>
