<template>
    <div class="card mb-4 p-3">
      <h3>People</h3>
      <p>Add people and specify their monthly income. You can also set a saving percentage to see how much is saved monthly.</p>
      <div class="row mb-2">
        <div class="col-4">
          <label><strong>Name</strong></label>
        </div>
        <div class="col-4">
          <label><strong>Income (SEK)</strong></label>
        </div>
        <div class="col-4">
          <label><strong>Saving (%)</strong></label>
        </div>
      </div>
      <div v-for="(person, index) in people" :key="index" class="d-flex align-items-center mb-2">
        <input v-model="person.name" class="form-control me-2" placeholder="Enter name" @input="saveData" />
        <input v-model.number="person.income" type="number" class="form-control me-2" placeholder="Enter monthly income" @input="saveData" />
        <div class="form-check form-switch me-2">
          <input type="checkbox" class="form-check-input" v-model="person.savingEnabled" @change="saveData" />
          <label class="form-check-label">Enable Savings</label>
        </div>
        <input
          v-if="person.savingEnabled"
          v-model.number="person.savingPercentage"
          type="number"
          class="form-control ms-2"
          placeholder="Default 10%"
          @input="saveData"
        />
        <button class="btn btn-danger ms-auto" @click="removePerson(index)">Remove</button>
      </div>
      <button class="btn btn-outline-primary mt-3" @click="addPerson">Add Person</button>
    </div>
  </template>
  
  <script>
  export default {
    props: ["people"],
    methods: {
      addPerson() {
        this.$emit("add-person");
      },
      removePerson(index) {
        this.$emit("remove-person", index);
      },
      saveData() {
        this.$emit("save-data");
      },
    },
  };
  </script>
  