<template>
    <div class="card p-3">
      <h3>Items</h3>
      <p>Add items (expenses) to split. You can choose to split the cost automatically based on income or manually set custom percentages.</p>
      <div v-for="(item, itemIndex) in items" :key="itemIndex" class="card mb-4 p-3">
        <div class="d-flex align-items-center mb-2">
          <input v-model="item.description" class="form-control me-2" placeholder="Enter item description (e.g., Rent)" @input="saveData" />
          <input v-model.number="item.totalAmount" type="number" class="form-control me-2" placeholder="Enter total cost in SEK" @input="saveData" />
          <div class="form-check form-switch me-2">
            <input type="checkbox" class="form-check-input" v-model="item.customSplitEnabled" @change="saveData" />
            <label class="form-check-label">Custom Split</label>
          </div>
          <button class="btn btn-danger ms-auto" @click="removeItem(itemIndex)">Remove</button>
        </div>
        <div v-if="item.customSplitEnabled" class="mt-3">
          <p class="small text-muted">Specify custom split percentages for each person. Ensure the total adds up to 100%.</p>
          <div v-for="(person, personIndex) in people" :key="personIndex" class="row align-items-center mb-2">
            <div class="col-6">
              <label>{{ person.name || `Person ${personIndex + 1}` }}'s Split (%)</label>
            </div>
            <div class="col-6">
              <input v-model.number="item.splits[personIndex]" type="number" class="form-control" placeholder="Split percentage" min="0" max="100" @input="saveData" />
            </div>
          </div>
        </div>
      </div>
      <button class="btn btn-outline-primary" @click="addItem">Add Another Item</button>
    </div>
  </template>
  
  <script>
  export default {
    props: ["items", "people"],
    methods: {
      addItem() {
        this.$emit("add-item");
      },
      removeItem(index) {
        this.$emit("remove-item", index);
      },
      saveData() {
        this.$emit("save-data");
      },
    },
  };
  </script>
  